table {
    border: none;
    border-collapse: collapse;
}

.totales{
    font-size: 13px;
}

table td {
    font-size: 12px;
    border: none!important;
    padding: 0.2rem!important;
}

.row-2, .row-4, .row-5, .row-6{
    width: 10%;
}

.row-0{
    width: 20%;
}

.row-3{
    width: 30%;
}

.row-1{
    width: 15%;
}

table tr {
    font-size: 14px;
    border: none!important;
    padding: 0.2rem!important;
}